.fiContainer {
    height: 80vh;
    background-color: black;

    overflow: auto;
}

.luxContainer { 
    height: 30vh;
    min-height: 900px;
    flex-direction: column;
}

.fiImage {
    width: 20vw;
    min-width: 470px;
    height: 50vh;

    margin-top: 12vh;
}

.whiteText { 
    p {
        color: white;
    }

    h2 {
        color: white;
    }
}

.styleDiv {
    text-align: left;
}

.styleAlignMarginSection {
    margin-left: auto;
    width: auto;
}

.styleAlignMarginH2 {
    width: auto;
    min-width: 450px;

}

.styleTextContainer {
    text-align: left;

    display: flex;
    flex-direction: row;

    align-items: center;
}

.lux-container {
    max-width: min-content;
}

.advContainer {
    background-color: #fafafa;
    justify-content: left;
    height: 35vh;
    min-height: 400px;
}

.listContainer {
    width: 30vw;
    min-width: 500px;
    text-align: left;
    margin-right: 10vw;
    

    ul.dashed > li:before {
        content: "––";
    }

    li span {
        position: relative;
        left: 0.3vw;
    }
}

.dashed {
    list-style-type: none;

    column-count: 2;
    column-gap: 4vw;

    li {
        margin-bottom: 2vh;
    }
}

.fImage {
    width: 100%;
    height: 50%;

    max-width: 100%;
    max-height: 50%;
}

.lobImage {
    width: auto;
    min-width: 900px;
    height: auto;

    max-width: 100%;
    max-height: 100%;
}

.advH2 {
    margin-right: auto;
    align-self: center;
    margin-bottom: 20vh;
}


@media all and (max-width: 900px) {
    .advH2 {
        margin: 0;
        margin-left: 3vw;
        align-self: center;
    }

    .luxContainer {
        padding-top: 0;
        min-height: 800px;
    }

    .dashed {
        column-gap: 10vw;
    }

    .fImage {
        min-height: 240px;
    }
    .lobImage {
        margin: auto;

        min-width: 500px;

        max-width: 100%;
        max-height: 50%;

        margin-top: 5vh;
    }

    .styleTextContainer {
        float: none;
        display: block;

        margin: 0;
        margin-left: 5vw;
        margin-bottom: 25vh;


        text-align: left;
    }


    .styleDiv {
        padding: 0;
        grid-row: 1;

    }

    .modifiedOverflow {
        width: 95vw;

        margin-right: 5vw;
        margin-left: 5vw;
    }

    .styleAlignMarginH2 {
        margin: 0;
        min-width: 320px;
        margin-right: 5vw;
    }
    .styleAlignMarginSection {
        margin: 0;
        min-width: 320px;
        margin-right: 5vw;
    }

    .fiImage {
        width: 95vw;

        height: 800px;
        margin: auto;

        min-width: 320px;
    }

    .listContainer {
        margin: 0;
        min-width: 320px;
        width: 100vw;
    }

}

@media all and (max-width: 640px) {
    .luxContainer {
        height: 50vh;
        min-height: 1000px;
    }

    .lobImage { 
        margin-top: 5px;
    }
    .fiImage {
        height: 500px;
    }
}

@media all and (max-width: 500px) {
    .lobImage {
        min-width: 100vw;
        height: 35vh;
    }

    .advContainer {
        min-height: 500px;
    }

    ul.dashed {
        column-count: 1;
    }
}